import React, { useState, useEffect } from "react";
import {
    ThemeProvider,
    createTheme,
    CssBaseline,
    Box,
    Typography,
    TextField,
    Container,
    Paper,
    Grid,
    Alert,
    Switch,
    FormControlLabel,
    Slider,
    Popover,
    IconButton,
    Tooltip,
} from "@mui/material";
import InfoOutlinedIcon from "@mui/icons-material/InfoOutlined";

function App() {
    //    (   )
    const theme = createTheme({
        palette: {
            mode: "dark",
            primary: {
                main: "#00e0ff", //    
            },
            secondary: {
                main: "#2c3e50", // - 
            },
        },
    });

    const savedValues = JSON.parse(localStorage.getItem("inputValues")) || {
        fuel: 0,
        additives: 0,
        currentMix: 0,
        targetMix: 0,
        ethanolPurity: 96, //     (96%)
    };
    //    
    const [fuel, setFuel] = useState(savedValues.fuel);
    const [additives, setAdditives] = useState(savedValues.additives);
    const [currentMix, setCurrentMix] = useState(savedValues.currentMix);
    const [targetMix, setTargetMix] = useState(savedValues.targetMix);
    const [ethanolPurity, setEthanolPurity] = useState(savedValues.ethanolPurity);

    //   
    const [error, setError] = useState("");
    const [ethanolResult, setEthanolResult] = useState(null);
    const [totalVolume, setTotalVolume] = useState(null);

    useEffect(() => {
        localStorage.setItem(
            "inputValues",
            JSON.stringify({ fuel, additives, currentMix, targetMix, ethanolPurity })
        );
    }, [fuel, additives, currentMix, targetMix, ethanolPurity]);
    
    //   Popover
    const [anchorEl, setAnchorEl] = useState(null);

    //    Popover
    const handleInfoClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    //    Popover
    const handleClose = () => {
        setAnchorEl(null);
    };

    //        
    const calculateResults = () => {
        if (fuel < 0) {
            setError("    .");
            return;
        }
        if (additives < 0) {
            setError("   .");
            return;
        }
        if (currentMix < 0) {
            setError("     .");
            return;
        }
        if (targetMix < 0) {
            setError("     .");
            return;
        }
        if (additives > 100 || currentMix > 100 || targetMix > 100) {
            setError("     100.");
            return;
        }
        if (targetMix < currentMix) {
            setError("      .");
            return;
        }
        setError(""); //  ,   //  ,   

        //   (/),  (/)   (/)
        const ethanolDensity = 0.789; //  
        const fuelDensity = 0.74; //  
        const waterDensity = 1.0; //  

        //   
        const pureFuel =
            fuel * (1 - additives / 100 - currentMix / 100); //   

        //     (   )
        const ethanolToAddRaw =
            (pureFuel * (targetMix / 100 - currentMix / 100)) / (1 - targetMix / 100);
        const ethanolToAdd = ethanolToAddRaw / (ethanolPurity / 100); //   

        //     
        const waterVolume = ethanolToAdd * ((100 - ethanolPurity) / 100);

        //      
        const totalFuelMass =
            pureFuel * fuelDensity + //   
            ethanolToAddRaw * ethanolDensity + //  
            waterVolume * waterDensity + //  
            (fuel * additives / 100) * fuelDensity; //  

        const totalFuelVolume = totalFuelMass / fuelDensity; //   

        setEthanolResult(ethanolToAdd.toFixed(2)); //  
        setTotalVolume(totalFuelVolume.toFixed(2)); //   
    };

    //  calculateResults     
    useEffect(() => {
        calculateResults();
    }, [fuel, additives, currentMix, targetMix, ethanolPurity]);
    
    return (
        <ThemeProvider theme={theme}>
            {/* CssBaseline      */}
            <CssBaseline />
            <Container maxWidth="sm" sx={{ mt: 5 }}>
                <Paper
                    elevation={3}
                    sx={{
                        p: 4,
                        borderRadius: 8,
                        backgroundColor: theme.palette.secondary.main,
                        color: theme.palette.primary.main,
                    }}
                >
                    <Box textAlign="center" mb={3}>
                        <Typography variant="h4" gutterBottom>
                             
                        </Typography>
                        <Typography variant="subtitle1" color="text.secondary">
                                  
                        </Typography>
                    </Box>

                    {/*     */}
                    {error && (
                        <Alert severity="error" sx={{ mb: 3 }}>
                            {error}
                        </Alert>
                    )}

                    {/*      */}
                    <Grid container spacing={2}>
                        {/*    */}
                        <Grid item size={{ xs: 12, md:12}}>
                            <Typography variant="h6"> </Typography>

                            {/*   ( ) */}
                            <Box mt={2}>
                                <Typography variant="body1">  ()</Typography>
                                <TextField
                                    fullWidth
                                    type="number"
                                    value={fuel}
                                    onChange={(e) => setFuel(parseFloat(e.target.value))}
                                    InputProps={{
                                        endAdornment: <Typography ml={1}></Typography>,
                                    }}
                                    sx={{
                                        "& .MuiOutlinedInput-root": {
                                            borderRadius: 0, //   
                                        },
                                    }}
                                />
                            </Box>

                            {/*  */}
                            <Box mt={2}>
                                <Typography variant="body1"> (%)</Typography>
                                <Slider
                                    value={additives}
                                    onChange={(event, newValue) => setAdditives(newValue)}
                                    step={1}
                                    min={0}
                                    max={100}
                                    valueLabelDisplay="auto"
                                    marks={[
                                        { value: 0, label: "0%" },
                                        { value: 100, label: "100%" },
                                    ]}
                                />
                            </Box>

                            {/*    */}
                            <Box mt={2}>
                                <Typography variant="body1">   (%)</Typography>
                                <Slider
                                    value={currentMix}
                                    onChange={(event, newValue) => setCurrentMix(newValue)}
                                    step={1}
                                    min={0}
                                    max={100}
                                    valueLabelDisplay="auto"
                                    marks={[
                                        { value: 0, label: "0%" },
                                        { value: 100, label: "100%" },
                                    ]}
                                />
                            </Box>

                            {/*    */}
                            <Box mt={2}>
                                <Typography variant="body1">   (%)</Typography>
                                <Slider
                                    value={targetMix}
                                    onChange={(event, newValue) => setTargetMix(newValue)}
                                    step={1}
                                    min={0}
                                    max={100}
                                    valueLabelDisplay="auto"
                                    marks={[
                                        { value: 0, label: "0%" },
                                        { value: 100, label: "100%" },
                                    ]}
                                />
                            </Box>

                            {/*   */}
                            <Box mt={2}>
                                <Typography variant="body1">  (%)</Typography>
                                <Slider
                                    value={ethanolPurity}
                                    onChange={(event, newValue) => setEthanolPurity(newValue)}
                                    step={1}
                                    min={0}
                                    max={100}
                                    valueLabelDisplay="auto"
                                    marks={[
                                        { value: 0, label: "0%" },
                                        { value: 100, label: "100%" },
                                    ]}
                                />
                            </Box>
                        </Grid>

                        {/*    */}
                        <Grid item size={{ xs: 12, md:12}}>
                            <Typography variant="h4" >
                                 :{" "}
                                <strong>{ethanolResult !== null ? ethanolResult : "-"}</strong>  .
                            </Typography>

                            <Box mt={3}>
                                

                                {/*    */}
                                <Box display="flex" alignItems="center" mt={2}>
                                    <Typography variant="h6">  </Typography>
                                    <IconButton
                                        size="small"
                                        onClick={handleInfoClick}
                                        sx={{ ml: 1 }}
                                    >
                                        <Tooltip title="  ">
                                            <InfoOutlinedIcon fontSize="small" />
                                        </Tooltip>
                                    </IconButton>
                                </Box>
                                <Typography variant="body1">
                                    {totalVolume !== null ? `${totalVolume} ` : "-"}
                                </Typography>
                            </Box>
                        </Grid>
                    </Grid>
                </Paper>
            </Container>

            {/* Popover   */}
            <Popover
                open={Boolean(anchorEl)}
                anchorEl={anchorEl}
                onClose={handleClose}
                anchorOrigin={{
                    vertical: "bottom",
                    horizontal: "left",
                }}
                transformOrigin={{
                    vertical: "top",
                    horizontal: "left",
                }}
                PaperProps={{
                    sx: {
                        backgroundColor: theme.palette.secondary.main,
                        color: theme.palette.primary.main,
                        p: 2,
                        borderRadius: 2,
                    },
                }}
            >
                <Typography variant="h6" gutterBottom>
                       
                </Typography>
                <Typography variant="body1">
                           :
                </Typography>
                <ul>
                    <li>
                        <Typography variant="body1">
                             : 0.74 /.
                        </Typography>
                    </li>
                    <li>
                        <Typography variant="body1">
                             : 0.789 /.
                        </Typography>
                    </li>
                    <li>
                        <Typography variant="body1">
                             : 1.0 /.
                        </Typography>
                    </li>
                    <li>
                        <Typography variant="body1">
                                  
                            (, 96%   4% ).
                        </Typography>
                    </li>
                    <li>
                        <Typography variant="body1">
                                 .
                        </Typography>
                    </li>
                </ul>
            </Popover>
        </ThemeProvider>
    );
}

export default App;